<template>
    <div class="home">
        <FilterNav @filterChange="current = $event" :current="current"/>
        <!-- if the project array has length, run the code in mounted -->
        <div v-if="projects.length">
            <div v-for="project in filteredProjects" :key="project.id">
                <SingleProject :project="project" @delete="handleDelete" @complete="handleComplete" />
            </div>
        </div>
    </div>
</template>

<script>
    import SingleProject from '../components/SingleProject.vue'
    import FilterNav from '../components/FilterNav.vue'

    export default {
        name: 'Home',
        components: { SingleProject, FilterNav },
        //return object of an empty array
        data() {
            return {
                projects: [],
                current: 'all'
            }
        },
        mounted() {
            //async
            //only running all this if the project array has a length
            fetch('http://localhost:3000/projects')
            .then(res => res.json())
            .then(data => this.projects = data)
            .catch(err => console.log(err.message))
        },
        methods: {
            handleDelete(id) {
                this.projects = this.projects.filter((project) => {
                    return project.id !== id
                })
            },
            handleComplete(id) {
                //callback function for each of the items inside the project
                //if it returns "true", it gives back the project, which we are storing in "p"
                //if it returns "false", it just continues to the next project
                let p = this.projects.find(project => {
                    return project.id === id
                })
                p.complete = !p.complete
            }
        },
        computed: {
            filteredProjects() {
                if (this.current === 'completed') {
                    return this.projects.filter(project => project.complete)
                }
                if (this.current === 'ongoing') {
                    return this.projects.filter(project => !project.complete)
                }
                return this.projects
            }
        }
    }
</script>